import pageStatus from '../../components/status/status.vue';
import location from '../../components/location/location.vue';
import alphabet from '../../components/alphabet/alphabet.vue';

import citys from '../../mock/citys.json';
import { mapGetters, mapMutations } from 'vuex';
import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			inputValue: '',
			focusFlag: false,
			cityInfo: {},
			keys: [],
			allCities: [],
			searchList: [],
			// 是否显示返回按钮



		};
	},
	components: {
		pageStatus,
		location,
		alphabet
	},
	computed: {
		...mapGetters(['city'])
	},
	onLoad() {
		this.initData();

		// 如果是小程序端 隐藏返回按钮
		this.showback = false;

	},
	watch: {
		inputValue() {
			this.searchCity();
		}
	},
	methods: {
		...mapMutations({
			setCity: 'SET_CITY'
		}),
		selectItem (name) {
			this.setCity(name);
			uni.navigateTo({
				url:'../../pages/address/address'
			})
		},
		initData() {
			this.request({
				url: interfaces.getCityData,
				success: res => {
					console.log(res);
					this.cityInfo = res;
					// this.cityInfo = citys;
					// 处理key 计算key
					// this.keys = Object.keys(citys);
					this.keys = Object.keys(res);
					// hotcities这个key移除掉
					this.keys.pop();
					// keys 排序
					this.keys.sort();

					// 存储所有城市, 用来搜索过滤
					this.keys.forEach(key => {
						// console.log(key);
						this.cityInfo[key].forEach(city => {
							// console.log(city);
							this.allCities.push(city);
						});
					});
				}
			});
		},
		onKeyInput: function(event) {
			this.inputValue = event.target.value;
		},
		onFocus() {
			this.focusFlag = true;
		},
		handelBack() {
			uni.navigateTo({
				url: '../address/address'
			});
		},
		searchCity() {
			console.log(this.inputValue);
			if (!this.inputValue) {
				// 如果搜索框为空, 数组置空
				this.searchList = [];
			} else {
				// 根据输入框的关键字检索城市 并存入到searchList数组中
				this.searchList = this.allCities.filter(item => {
					return item.name.indexOf(this.inputValue) != -1;
				});
				console.log(this.searchList);
			}
		}
	}
};
